import { useForm } from "react-hook-form";
import { Icon } from "@iconify/react";
import { useEffect, useMemo } from "react";

import useAuth from "../../hook/useAuth";
import { SEND_POST_REQUEST,  API_CLIENT } from "../../utils/API";
import toast from 'react-hot-toast';
import Page from '../../components/Page';
import { Link } from "react-router-dom";
import { t } from "i18next";


export default function Feedback() {

    const defaultValues = useMemo(() => ({
        name: "",
        email:"",
        phone:"",
        feedback: "",
    }), []);
    const { register,  handleSubmit, formState: { isSubmitting } } = useForm(defaultValues);

    const onSubmit = (data) => {
        SEND_POST_REQUEST(API_CLIENT.user.setFeedback, data).then(res => {
            if (res.status === 200) {
                toast.success(res.message);
                
            }
            else {
                toast.error(res.message);
            }

        });

    }
 
    return (
        <Page title="Profile" className="flex flex-col w-full gap-2 sm:gap-4 ">
            <div className="breadcrumbs  bg-base-300 absolute w-full left-0 px-6 overflow-x-hidden mt-24">
                <ul>
                    <li>
                        <Link to='/'>{t('words.home')}</Link>
                    </li>
                    <li>
                        <Link to='/user'>{t('menu.shopping')}</Link>
                    </li>
                    <li>
                        <label >{t('menu.contact')}</label>
                    </li>
                </ul>
            </div>
            <div className="mt-40 w-full"></div>
            <div className="container px-5 py-8 text-gray-700 max-w-xl">
                <form onSubmit={handleSubmit(onSubmit)} className="flex p-4 sm:p-8 flex-col gap-4 rounded-2xl shadow-lg bg-base-300 ">
                    <img src = '/assets/index-feedback.svg' alt = '' className="h-40"/>
                    <p>
                        If you send us your feedback and information about the type of product you want to order, we will introduce new products based on your overall feedback. Thank you.
                    </p>
                    <div className="w-full">
                        <p className="font-bold text-sm pl-2"> {`Name*`}</p>
                        <input className="input h-10 border border-base-300 w-full" required 
                            {...register("name")} />
                    </div>
                    <div className="w-full">
                        <p className="font-bold text-sm pl-2"> {`E-mail*`}</p>
                        <input className="input h-10 border border-base-300 w-full" required
                            {...register("email")} />
                    </div>
                    <div className="w-full">
                        <p className="font-bold text-sm pl-2"> {`Phone number*`}</p>
                        <input className="input h-10 border border-base-300 w-full " required
                            {...register("phone")} />
                    </div>
                    <div className="w-full">
                        <p className="font-bold text-sm pl-2" > {`Feedback`}</p>
                        <textarea rows={4} className="textarea  border border-base-300 w-full" required
                            {...register("feedback")} />
                    </div>

                    <div className="flex ">
                        <button type="submit" className={`btn btn-accent px-5 ml-auto ${isSubmitting && 'loading'}`}>SEND</button>
                    </div>

                </form>
            </div>
        </Page>
    )


}